<template>
<card>
    <item-list :com-data="itemList"></item-list>
    <div>
        <label style="color:#333;font-size:14px;"></label>
        <el-select v-model="statuVal" placeholder="">
            <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
    </div>
</card>
</template>
<script>
import {detail}  from '@/api/basicInfo/logisticsEnterprise/detail'
import {save}  from '@/api/basicInfo/logisticsEnterprise/edit'
export default {
    data(){
        return {
            statuVal:'',
            itemList: {
                data:{},
                list: [
                    {
                        label:'',
                        prop:'name'
                    },
                    {
                        label:'',
                        prop:'out_status_name	'
                    }
                ]
            },
            options:[
                {
                    label:'',
                    value:'1'
                },
                {
                    label:'',
                    value:'0'
                }
            ]
        }
    },
    mounted(){
        if(this.$route.query.logistics_id){
            detail(this.$route.query.logistics_id)
            .then(res => {
                if(res.data === 0) {
                     this.itemList.data = $utils.getDeepKey(res,'data.data')
                }  
            })
            .catch(err => {
                let res = {
                    "state": 0,
                    "data": {
                        "search": null,
                        "data": {
                            "logistics_id": "1392",
                            "out_status_name": "",
                            "name": "",
                            "status_name": "",
                            "status": "1",
                            "is_can_edit": false,
                        }
                    }
                }
                this.itemList.data = $utils.getDeepKey(res,'data.data')
                this.statuVal = $utils.getDeepKey(res,'data.data.status')
            })
        }
    }
}
</script>

